body {
  margin: 0;
  margin-top: 3rem;
  padding: 0;
}

.col{
    background: #eee;
    color:black;
    border: 1px #921029 solid;
    border-left: none; border-bottom: none;
    box-sizing: border-box;
    cursor: pointer;
    width: 5rem; height: 4rem;
    display:flex;
    align-items: center;
    font-size: 1.4rem;
    justify-content: center;
}

.zero{
    width: 10rem;
}

.operator{
    background: #F2274F;
    font-size: 2rem;
}
#display-wrapper{
    width: 20rem;
    height: 5rem;
    border-right: 1px #921029 solid;
    background: black;
    position: relative;
}

#display{
    color:white;
    font-size: 2rem;
    position: absolute;
    right: 0;
    bottom: 0;
}
.container{
    width: 20rem;
    margin: 0 auto;
    height: auto;
    border: 1px #921029 solid;
    border-right: none;
}

.row{
    display:flex;
    width: 20rem;
}
